.ss-app-config-heading {
    font-family: "Montserrat", sans-serif;
    font-size: 14px;
    font-weight: bold;
    padding: 8px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;

    span:first-child > * {
        vertical-align: middle;
    }

    &.affixed {
        background: @white;
        margin: 0 -32px;
        padding: 8px 32px;
    }
}

.ss-app-config-links {
    display: flex;
    flex-wrap: wrap;
    margin-left: -12px;
    margin-right: -12px;
    max-width: 1200px;

    .link-item-wrapper {
        padding: 12px;
        width: 124px;
    }

    .ss-app-config-nav-item {
        background-color: @blue;
        border-radius: 8px;
        height: 100px;
        margin-bottom: 0;
        padding: 56px 12px 12px;
        text-align: center;

        .shadow();

        .link-icon {
            display: inline-block;
            width: 36px;
            height: 36px;
            top: 18px;
            left: 50%;
            margin-left: -18px;
        }

        .ss-app-config-nav-item-label {
            display: flex;
            margin: 0;
            opacity: 1;
            white-space: normal;
            align-items: center;
            height: 32px;
            width: 100%;
            padding: 0;
            justify-content: center;
            font-size: 12px;
        }
    }
}

.ss-app-config-tabs-nav {
    .link-item-wrapper {
        padding: 12px;
        width: 124px;
    }

    .ss-app-config-nav-item {
        color: @gray2;
        border-radius: 8px;
        margin-bottom: 0;
        text-align: left;
        padding: 4px 4px 4px 32px;

        .ss-app-config-nav-item-label {
            margin: 0;
            opacity: 1;
            padding: 0;
            font-size: 14px;
            display: inline-block;
            width: auto;
            max-width: 100%;
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
        }

        & svg:not(.menu-edge) {
            left: 0;
            top: 0;
        }
    }
}

.ss-app-config-icon-select-overlay {
    position: fixed;
    top: 0;
    left: 0;
    &.open {
        right: 0;
        bottom: 0;
        z-index: 98;
    }
}

.ss-app-config-icon-set {
    position: relative;
    border-radius: 6px;
    background: @white;
    max-height: 280px;
    overflow-x: hidden;
    overflow-y: auto;
    .shadow();

    svg[class*="ss-app-config-ico"] {
        line-height: 36px;
        display: inline-block;
    }
}

.ss-app-config-icon-set-wrap {
    display: flex;
    flex-wrap: wrap;
    width: 320px;
    height: 100%;
    position: relative;
    padding: 16px;
}

.ss-app-config-icon-item {
    width: 20%;
    text-align: center;
    padding: 4px 0;
    cursor: pointer;
    color: @gray2;

    > div {
        font-size: 9px;
    }
}

.ss-app-config-icon-selected {
    width: 100px;
    display: inline-flex;
    align-items: center;
    cursor: pointer;

    svg:not(.menu-edge) {
        position: static;
    }

    svg[class*="ss-app-config-ico"] {
        margin-right: 12px;
    }
}

.ss-app-config-icon-select-label {
    font-size: 10px;
    text-align: center;
    width: 60px;
}

.ss-app-config-form-horizontal {
    .ss-app-config-form-item-label {
        width: 100px;
        text-align: left;
    }

    .ss-app-config-form-item-control {
        max-width: 480px;
    }
}

.ss-app-config-group-links {
    display: flex;
    justify-content: space-between;

    h4 {
        flex-grow: 2;
        margin-bottom: 20px;
    }
}

.ss-app-config-nav-links {
    display: flex;
    justify-items: stretch;
    flex-wrap: wrap;
    margin: 0 -8px;

    > div {
        padding: 8px;
        @media screen and (min-width: @screen-xxl-min) {
            width: 33.33%;
        }
        @media screen and (min-width: @screen-lg-min) and (max-width: @screen-xxl) {
            width: 50%;
        }
        @media screen and (max-width: @screen-lg) {
            width: 100%;
        }
    }

    .ss-app-config-menu-form-fields {
        border-radius: @border-radius-base;
        border: 1px solid @border-color-base;
        padding: 20px 24px;
    }
}

.ss-app-config-dbnav-item {
    position: relative;
}

.ss-app-config-dbnav-item-delete {
    position: absolute;
    top: -4px;
    right: -4px;
    box-shadow: none;
}

.ss-app-config-dashboard {
    padding: 20px 32px 20px 20px;
}

.ss-app-config-form-list {
    > h3 {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 24px;
    }
}

.ss-app-config-tabs-tab {
    background-color: #f7f7f7;
    border-top-left-radius: 6px;
    border-bottom-left-radius: 6px;
    margin-top: 4px !important;
    width: @main-nav-width-collapse;

    &.ss-app-config-tabs-tab-active .ss-app-config-nav-item {
        color: @gray1;

        svg[class*="ss-app-config-ico"] {
            margin-right: 12px;
        }
    }
}
