.ss-app-config-collapse {
    border-radius: 10px;
    border-color: transparent;

    .ss-app-config-collapse-header {
        font-size: 16px;
        font-weight: bold;

        .detail-marker();
    }

    > .ss-app-config-collapse-item {
        border-bottom: 0;

        &.ss-app-config-collapse-item-active .ss-app-config-collapse-header > .symbol-block {
            border-radius: 10px 0 0 0;
        }
    }

    &.ss-app-config-collapse-icon-position-right > .ss-app-config-collapse-item > .ss-app-config-collapse-header {
        padding-left: 36px;
        &::before {
            background-color: transparent;
        }
        svg {
            position: absolute;
            right: 12px;
            top: 50%;
            margin-top: -12px;
        }
    }

    &.ss-app-config-collapse-ghost {
        .ss-app-config-collapse-header {
            padding: 0 !important;
            margin-bottom: 12px;
        }

        .ss-app-config-collapse-content {
            background-color: @white1;
        }

        > .ss-app-config-collapse-item > .ss-app-config-collapse-header svg {
            right: 0;
        }
    }

    > .ss-app-config-collapse-item > .ss-app-config-collapse-header .ss-app-config-collapse-extra {
        padding-right: 12px;
    }
}

.ss-app-config-collapse-content {
    position: relative;
}

.ss-app-config-btn {
    &.ss-app-config-btn-sm:not(.ss-app-config-btn-circle) {
        border-radius: 6px;
        font-size: 10px;
    }
    > * {
        vertical-align: middle;
    }

    &.ss-app-config-btn-icon-only {
        padding: 0;
    }

    .anticon {
        font-size: 16px;
    }

    @media screen and (max-width: @screen-sm-max) {
        padding: 6px 12px;
        line-height: 20px;
        font-size: 14px;
        height: 32px;
    }

    .ss-app-config-modal-footer & {
        min-width: 70px;
    }
}

.ss-app-config-form-item-label {
    > label.ss-app-config-form-item-required:not(.ss-app-config-form-item-required-mark-optional):before {
        content: "";
    }
    > label.ss-app-config-form-item-required:not(.ss-app-config-form-item-required-mark-optional):after {
        color: @red;
        content: "*";
        display: inline-block;
        font-family: SimSun, sans-serif;
        font-size: 16px;
        line-height: 1;
        margin-right: 4px;
    }
}

.ss-app-config-tabs-tab {
    display: block;
}

.ss-app-config-input {
    font-size: 12px;
}
