.ss-app-config-main-header {
	font-family: "Montserrat", sans-serif;
	position: fixed;
	height: 82px;
	border-bottom: 1px solid @light-gray1;
	height: @main-header-height;
	top: 0;
	left: 0;
	right: 0;
	background-color: @white1;
	z-index: @main-header-zindex;

	> div {
		padding: 12px 36px;
		display: flex;
		width: 100%;
		height: @main-header-height;
		justify-content: space-between;
		align-items: center;
	}

	h1 {
		font-size: 20px;
		line-height: 20px;
		margin: 0;
	}
}

.ss-app-config-user-top-nav {
	white-space: nowrap;
	margin-right: -8px;

	> * {
		vertical-align: middle;
	}
}

.ss-app-config-user-name {
	font-family: "Montserrat", sans-serif;
	text-align: right;
	width: 50vw;
	max-width: 120px;
	padding-right: 8px;
	white-space: nowrap;

	.ss-app-config-user-dd {
		vertical-align: middle;
		line-height: 12px;
		cursor: pointer;
	}
}

.ss-app-config-dd-profile {
	min-width: 125px;

	a {
		font-family: "Montserrat", sans-serif;
		font-size: 12px;
		color: @black;
		> * {
			vertical-align: middle;
			display: inline-block;
		}
		svg {
			margin-right: 8px;
		}
	}
}

@media screen and (min-width: @screen-xl) {
	.ss-app-config-user-top-nav .ss-app-config-btn.menu-burger {
		display: none;
	}
}

@media screen and (min-width: @screen-md-min) {
	.ss-app-config-mobile-name {
		display: none;
	}
}

@media screen and (max-width: @screen-sm-max) {
	.ss-app-config-user-dd,
	.ss-app-config-main-header h1,
	.ss-app-config-user-top-nav .ss-app-config-btn:not(:last-child) {
		display: none;
	}

	.ss-app-config-main-header,
	.ss-app-config-main-header > div {
		height: 52px;
	}
	.ss-app-config-main-header {
		background: linear-gradient(90deg, #1268b3 30%, #01abec 100%);
	}

	.ss-app-config-main-header {
		> div {
			display: block;
			padding: 12px;
		}
	}

	.ss-app-config-user-top-nav {
		display: flex;
		align-items: center;
		flex-flow: row-reverse;
		justify-content: flex-end;
	}

	.ss-app-config-user-top-nav .ss-app-config-btn:last-child {
		padding: 0;
		margin-left: 0 !important;

		svg {
			height: 22px;
			path {
				stroke: @white1;
			}
		}
	}

	.ss-app-config-user-name {
		font-size: 20px;
		color: @white1;
		line-height: 20px;
		font-weight: 600;
		width: calc(100% - 40px);
		max-width: 100%;
		text-align: left;
		text-overflow: ellipsis;
		overflow: hidden;
	}
}

@media screen and (max-width: @screen-xs) {
	.ss-app-config-user-name {
		font-size: 16px;
	}
}
